.page {
  font-family: Manrope, sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: $main-text-color;

  scroll-behavior: smooth;

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transform: translateX(-100%);
    transition:
      opacity 0.5s ease,
      transform 0.5s ease;
    pointer-events: none;

    &:target {
      opacity: 1;

      transform: translateX(0%);
      pointer-events: all;
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
